<template>
    <div class="page page-user-bill-detail">

        <div class="status">
            <div class="icon">
                <van-icon name="success" />
            </div>
            <div class="statusText">成功</div>
        </div>

        <div class="date">
            {{ info.createTime }}
        </div>
        <van-cell-group>
            <van-cell title="账变类型">
                {{ info.typeName }}
            </van-cell>
            <van-cell title="账变金额">
                {{ parseFloat(info.amount).toFixed(2) }}
            </van-cell>
        </van-cell-group>

        <van-cell-group>
            <van-cell title="收款用户昵称">
                {{ info.receiverNickName }}
                <button v-clipboard="info.receiverNickName || ''" class="copy copyBtn" @success="$root.copySuccessful"></button>
            </van-cell>
            <van-cell title="付款用户昵称">
                {{ info.payNickName }}
                <button v-clipboard="info.payNickName || ''" class="copy copyBtn" @success="$root.copySuccessful"></button>
            </van-cell>
        </van-cell-group>

        <van-cell-group>
            <van-cell title="账变流水号">
                <div class="nowrap">
                    {{ info.businessNo }}
                </div>
                <button v-clipboard="info.businessNo || ''" class="copy copyBtn" @success="$root.copySuccessful"></button>
            </van-cell>
        </van-cell-group>
    </div>
</template>

<script>
import { myChangeDetail } from '@/apis/user'
export default {
    data: () => {
        return {
            info: {
                amount: "",
                businessNo: "",
                createTime: "",
                receiverId: "",
                receiverType: "",
                receiverWalletAddr: "",
                type: "",
                typeName: "",
                userWalletAddr: "",
            }
        }
    },
    created() {
        this.$root.$emit("root:set-topbar", ["left-back"]);
        this.getDetail()
    },
    methods: {
        async getDetail() {
            const { ok, data, msg } = await myChangeDetail({ id: this.$route.query.id })
            if (!ok) this.$toast.fail(msg)
            this.info = data.list
        }
    },
}
</script>

